.login-box-container {
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #202020;
  width: 80%;
  max-width: 450px;
  border-radius: 8px;
  padding: 20px;
  padding-bottom: 40px;
}

.login-logo {
  width: 120px;
  color: #e90f0f;
  font-family: "Bree Serif";
  font-size: 32px;
  font-weight: 700;
  margin-top: 10px;
  margin-bottom: 12px;
}

.label {
  color: #ffffff;
  font-family: "Roboto";
  font-size: 12px;
  font-weight: 400;
  margin-top: 10px;
  margin-bottom: 8px;
}

.form-container {
  display: flex;
  flex-direction: column;
  width: 90%;
}

.input {
  color: #ffffff;
  background-color: transparent;
  font-family: "Roboto";
  font-size: 12px;
  font-weight: 300;
  margin-bottom: 12px;
  height: 30px;
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #2c364c;
  outline: none;
}

.login-button {
  color: #ffffff;
  background-color: #4f46e5;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 400;
  margin-top: 16px;
  margin-bottom: 8px;
  height: 30px;
  padding: 4px 8px;
  border-radius: 4px;
  border: none;
  outline: none;
  cursor: pointer;
}

.error-msg {
  color: #ff0b37;
  font-family: "Roboto";
  font-size: 10px;
}

.link {
  text-decoration: underline;
}

.new-user {
  color: #16d135;
  font-family: "Roboto";
  font-size: 24px;
}
